* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    font-family: 'Helvetica', sans-serif;
    line-height: 1.5;
    background: lightseagreen;
    position: relative;
}

.todo-container {
    max-width: 700px;
    margin: 50px auto 0px auto;
    padding: 10px;
    min-height: 400px;
    background: palevioletred;
    border-radius: 4px;
    box-shadow: 1px 1px 2px rgba(0, 0, 0, .6);
    position: relative;
}

.form-group {
    margin-bottom: 35px;
    display: flex;
}

.form-control {
    flex: 1 1;
    padding: 12px;
    border: none;
    outline: none;
    color: #555;
    border-top-left-radius: 7px;
    border-bottom-left-radius: 7px;
    transition: background-color .3s ease;
}

.button {
    cursor: pointer;
    border: none;
    outline: none;
    color: #fff;
    background: lightskyblue;
    font-size: 20px;
    padding: 9px 17px;
    border-top-right-radius: 7px;
    border-bottom-right-radius: 7px;
}

.button-modal {
    cursor: pointer;
    border: none;
    outline: none;
    color: #fff;
    background: lightskyblue;
    font-size: 20px;
    padding: 9px 17px;
    border-radius: 4px;
}

.todo-list {
    list-style-type: none;
}

.todo-item {
    background: #fff;
    padding: 7px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-radius: 7px;
    margin-bottom: 15px;
    /* cursor: pointer; */
}

.todo-item .controls {
    display: flex;
    align-items: center;
}

.todo-item .remove {
    display: flex;
    width: 18px;
    height: 18px;
    align-items: center;
    justify-content: center;
    margin-left: 10px;
    background: #f3c2b1;
    color: #fff;
    font-size: 15px;
    cursor: pointer;
}

.todo-item.checked p {
    text-decoration: line-through;
}

.todo-item.checked {
    background: lightskyblue;;
}

.check.checked {
    background: #a0e7a0;
}

.check {
    display: block;
    width: 18px;
    height: 18px;
    background: #ecebeb;
    cursor: pointer;
}

.modal {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    width: 500px;
    background: #189296;
    color: #fff;
    text-align: center;
    padding: 15px;
    border-radius: 4px;
    z-index: 50;
}

.modal p {
    margin-bottom: 14px;
}

.modal h1 {
    margin-bottom: 14px;
    text-decoration: underline;
}

.backdrop {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.6);
    z-index: 25;
}


@media (max-width: 578px) {
    .modal {
        width: 90%;
    }
}

@media (max-width: 300px) {
    .form-group {
        display: block;
    }

    .form-control {
        display: block;
        width: 100%;
        border: none;
        border-radius: 4px;
        flex: none;
    }

    .form-group .button {
        position: absolute;
        top: 75%;
        left: 50%;
        -webkit-transform: translateX(-50%);
                transform: translateX(-50%);
        border-radius: 50%;
        width: 60px;
        height: 60px;
        font-size: 40px;
    }
}
